import {action, computed, observable} from "mobx";
import {
    IApplicantMedia,
    IEducationBackground,
    IEmergencyContact,
    IHealthCondition,
    IPersonalDetails,
    IPersonalStatement
} from "../constants/interfaces";
import {FirebaseApp} from "../firebase";
import DynamicResponse from "../utils/DynamicResponse";

function isValid(value?: any, stringType: boolean = true, minChar: number = 1): boolean {
    return !(value === null || value === undefined || (stringType && value.toString().trim().length < minChar));
}

export class UserStore {
    @observable loadingInitialize: boolean = false;
    @observable loading: boolean = false;
    private firebaseApp = FirebaseApp.getInstance();
    @observable private _dataPersonalDetails: IPersonalDetails = {};
    @observable private _dataEducationBackground: IEducationBackground = {};
    @observable private _dataHealthCondition: IHealthCondition = {};
    @observable private _dataPersonalStatement: IPersonalStatement = {};
    @observable private _dataEmergencyContact: IEmergencyContact = {};
    @observable private _dataApplicantMedia: IApplicantMedia = {};

    get personalDetails(): IPersonalDetails {
        return this._dataPersonalDetails;
    }

    get educationBackground(): IEducationBackground {
        return this._dataEducationBackground;
    }

    get healthCondition(): IHealthCondition {
        return this._dataHealthCondition;
    }

    get personalStatement(): IPersonalStatement {
        return this._dataPersonalStatement;
    }

    get emergencyContact(): IEmergencyContact {
        return this._dataEmergencyContact;
    }

    get applicantMedia(): IApplicantMedia {
        return this._dataApplicantMedia;
    }

    @computed
    get personalDetailsIsValid(): boolean {
        return isValid(this._dataPersonalDetails.fullName) &&
            isValid(this._dataPersonalDetails.address) &&
            isValid(this._dataPersonalDetails.city) &&
            isValid(this._dataPersonalDetails.province) &&
            isValid(this._dataPersonalDetails.country) &&
            isValid(this._dataPersonalDetails.postcode) &&
            isValid(this._dataPersonalDetails.phone) &&
            isValid(this._dataPersonalDetails.tShirtSize) &&
            isValid(this._dataPersonalDetails.dateOfBirth, false) &&
            isValid(this._dataPersonalDetails.sex) &&
            isValid(this._dataPersonalDetails.maritalStatus) &&
            isValid(this._dataPersonalDetails.passportNumber) &&
            isValid(this._dataPersonalDetails.expiryDate, false) &&
            isValid(this._dataPersonalDetails.nationality);
    }

    @computed
    get educationBackgroundIsValid(): boolean {
        return isValid(this._dataEducationBackground.latestDegree) &&
            isValid(this._dataEducationBackground.homeUniversity) &&
            isValid(this._dataEducationBackground.country);
    }

    @computed
    get healthConditionIsValid(): boolean {
        return isValid(this._dataHealthCondition.disability) &&
            isValid(this._dataHealthCondition.dietaryRestriction);
    }

    @computed
    get personalStatementIsValid(): boolean {
        return isValid(this._dataPersonalStatement.informationSources) &&
            isValid(this._dataPersonalStatement.motivation) &&
            isValid(this._dataPersonalStatement.personalExperiences) &&
            isValid(this._dataPersonalStatement.specialitiesDescribe) &&
            isValid(this._dataPersonalStatement.afterProgram);
    }

    @computed
    get emergencyContactIsValid(): boolean {
        return isValid(this._dataEmergencyContact.fullName) &&
            isValid(this._dataEmergencyContact.relationship) &&
            isValid(this._dataEmergencyContact.email) &&
            isValid(this._dataEmergencyContact.phone) &&
            isValid(this._dataEmergencyContact.address) &&
            isValid(this._dataEmergencyContact.city) &&
            isValid(this._dataEmergencyContact.province) &&
            isValid(this._dataEmergencyContact.country) &&
            isValid(this._dataEmergencyContact.postcode);
    }

    @computed
    get applicantMediaIsValid(): boolean {
        return isValid(this._dataApplicantMedia.passportPhoto) &&
            isValid(this._dataApplicantMedia.passportScan) &&
            isValid(this._dataApplicantMedia.medicalCertificate) &&
            isValid(this._dataApplicantMedia.declarationLetter);
    }

    @computed
    get applicationFormIsValid(): boolean {
        return this.personalDetailsIsValid &&
            this.educationBackgroundIsValid &&
            this.healthConditionIsValid &&
            this.personalStatementIsValid &&
            this.emergencyContactIsValid;
    }

    @action
    clear() {
        this._dataPersonalDetails = {};
        this._dataEducationBackground = {};
        this._dataHealthCondition = {};
        this._dataPersonalStatement = {};
        this._dataEmergencyContact = {};
    }

    @action
    async initialize(): Promise<DynamicResponse> {
        let response = new DynamicResponse();
        this.loadingInitialize = true;
        try {
            const result = await this.firebaseApp.fetchApplicationForm();

            this._dataPersonalDetails = result.personalDetails;
            this._dataEducationBackground = result.educationBackground;
            this._dataHealthCondition = result.healthCondition;
            this._dataPersonalStatement = result.personalStatement;
            this._dataEmergencyContact = result.emergencyContact;
            this._dataApplicantMedia = result.applicantMedia;

            response.success();
        } catch (e) {
            response.error = (e);
        }

        this.loadingInitialize = false;
        return response;
    }

    @action
    async updatePersonalDetails(personalDetails: IPersonalDetails): Promise<DynamicResponse> {
        const res = await this.firebaseApp?.updatePersonalDetails(personalDetails);
        if (!res.hasError())
            this._dataPersonalDetails = personalDetails;

        return res;
    }

    @action
    async updateEducationBackground(educationBackground: IEducationBackground): Promise<DynamicResponse> {
        const res = await this.firebaseApp?.updateEducationBackground(educationBackground);
        if (!res.hasError())
            this._dataEducationBackground = educationBackground;

        return res;
    }

    @action
    async updateHealthCondition(healthCondition: IHealthCondition): Promise<DynamicResponse> {
        const res = await this.firebaseApp?.updateHealthCondition(healthCondition);
        if (!res.hasError())
            this._dataHealthCondition = healthCondition;

        return res;
    }

    @action
    async updatePersonalStatement(personalStatement: IPersonalStatement): Promise<DynamicResponse> {
        const res = await this.firebaseApp?.updatePersonalStatement(personalStatement);
        if (!res.hasError())
            this._dataPersonalStatement = personalStatement;

        return res;
    }

    @action
    async updateEmergencyContact(emergencyContact: IEmergencyContact): Promise<DynamicResponse> {
        const res = await this.firebaseApp?.updateEmergencyContact(emergencyContact);
        if (!res.hasError())
            this._dataEmergencyContact = emergencyContact;

        return res;
    }

    @action
    async updateApplicantMedia(applicantMedia: IApplicantMedia): Promise<DynamicResponse> {
        const res = await this.firebaseApp?.updateApplicantMedia(applicantMedia);
        if (!res.hasError())
            this._dataApplicantMedia = applicantMedia;

        return res;
    }
}
